import {
  Box, Drawer, DrawerBody, DrawerCloseButton,
  DrawerContent, DrawerFooter, DrawerHeader, DrawerOverlay,
  Icon, useDisclosure, VStack,
} from '@chakra-ui/react';
import React, { ReactElement, useEffect } from 'react';
import { IconType } from 'react-icons';
import { GiHamburgerMenu } from 'react-icons/gi';
import { MenuItemLink } from './MenuItemLink';

interface SidebarProps {
  routes: Array<{
    component: ReactElement,
    basePath: string,
    path: string,
    exact?: boolean,
    pageTitle: string,
    icon?: IconType,
  }>;
}

function useWindowSize() {
  // Initialize state with undefined width/height so server and client renders match
  // Learn more here: https://joshwcomeau.com/react/the-perils-of-rehydration/
  const [windowSize, setWindowSize] = React.useState<{
    width: number;
    height: number;
  }>({
    width: 0,
    height: 0,
  });
  useEffect(() => {
    // Handler to call on window resize
    function handleResize() {
      // Set window width/height to state
      setWindowSize({
        width: window.innerWidth,
        height: window.innerHeight,
      });
    }
    // Add event listener
    window.addEventListener('resize', handleResize);
    // Call handler right away so state gets updated with initial window size
    handleResize();
    // Remove event listener on cleanup
    return () => window.removeEventListener('resize', handleResize);
  }, []); // Empty array ensures that effect is only run on mount
  return windowSize;
}

export const Sidebar = ({ routes }: SidebarProps) => {
  const size = useWindowSize();
  const minWidth = 1400;
  const isLargerThanMinWidth = size.width > minWidth;

  const { isOpen, onOpen, onClose } = useDisclosure({
    defaultIsOpen: isLargerThanMinWidth,
  });

  return (
    <>
      <div className="drawer-button">
        <Box pt={3} pb={3} pl={4} pr={4} cursor="pointer" onClick={onOpen}>
          <Icon as={GiHamburgerMenu} />
        </Box>
      </div>
      <Drawer
        isOpen={isLargerThanMinWidth || isOpen}
        placement="left"
        onClose={() => {
          if (!isLargerThanMinWidth) {
            onClose();
          }
        }}
        blockScrollOnMount={!isLargerThanMinWidth}
        variant={isLargerThanMinWidth ? 'alwaysOpen' : undefined}
        trapFocus={!isLargerThanMinWidth}
      >
        {!isLargerThanMinWidth && <DrawerOverlay />}
        <DrawerContent>
          {!isLargerThanMinWidth && <DrawerCloseButton />}
          <DrawerHeader>Menu</DrawerHeader>

          <DrawerBody>
            <VStack spacing={0}>
              {routes.map(({
                pageTitle, path, exact, icon,
              }) => (
                <MenuItemLink
                  to={path}
                  label={pageTitle}
                  activeOnlyWhenExact={exact}
                  key={pageTitle}
                  icon={icon}
                />
              ))}
            </VStack>
          </DrawerBody>

          <DrawerFooter />
        </DrawerContent>
      </Drawer>
    </>
  );
};
