import {
  Accordion, AccordionButton, AccordionIcon, AccordionItem,
  AccordionPanel, Box, Button, FormControl, FormLabel, Grid,
  GridItem, HStack, Icon, SimpleGrid, Spacer, Text, Tooltip, VStack,
} from '@chakra-ui/react';
import { Form, Formik, useFormikContext } from 'formik';
import { AiFillDelete } from 'react-icons/ai';
import { GiDiceTwentyFacesTwenty } from 'react-icons/gi';
import { ImRedo } from 'react-icons/im';
import { IoMdSave } from 'react-icons/io';
import React from 'react';
import { useParams } from 'react-router-dom';
import { NumberInputControl, SwitchControl } from '../common/formik-chakra';
import TextareaControl from '../common/formik-chakra/TextareaControl';
import { useAppDispatch } from '../common/hooks';
import { modifyPlayerValidationSchema, modifyPlayer, removePlayer } from '../common/playerSlice';
import { Dice, rollDice } from '../common/util';
import { ModifyCharacterConditionsField } from './ModifyCharacterConditionsField';
import { Player } from './Player.interfaces';

interface ModifyPlayerFormProps {
  player: Player;
}

export const ModifyPlayerForm = ({ player }: ModifyPlayerFormProps) => {
  const dispatch = useAppDispatch();

  const params = useParams<{id: string}>();

  const modifyPlayerAction = (p: Player) => dispatch(modifyPlayer(p));
  const removePlayerAction = (id: string) => dispatch(removePlayer(id));

  const PlayerModifyStatFields = () => {
    const editStatFields: Array<[string, string]> = [
      ['AC: ', 'armourClass'],
      ['Initiative: ', 'initiative'],
      ['Init. Mod: ', 'initiativeMod'],
      ['Max HP: ', 'maxHP'],
    ];

    const { setFieldValue } = useFormikContext<Player>();
    return (
      <AccordionItem>
        <AccordionButton>
          <Box flex="1" textAlign="left">
            <strong>Edit Stats</strong>
          </Box>
          <AccordionIcon />
        </AccordionButton>
        <AccordionPanel>
          <Grid
            fontSize="sm"
            templateColumns="repeat(4, 1fr)"
            templateRows="repeat(2, 1fr)"
            gap={4}
          >
            <GridItem
              colSpan={4}
            >
              <Button
                width="100%"
                onClick={() => setFieldValue('initiative', rollDice(Dice.d20))}
                variant="outline"
                rightIcon={<Icon as={GiDiceTwentyFacesTwenty} />}
              >
                Roll Initiative
              </Button>
            </GridItem>
            {
              editStatFields.map(([displayString, value]) => (
                <GridItem key={value}>
                  <HStack>
                    <Text>{displayString}</Text>
                    <NumberInputControl hideError name={value} width="8em" />
                  </HStack>
                </GridItem>
              ))
            }
          </Grid>
        </AccordionPanel>
      </AccordionItem>
    );
  };

  const PlayerModifyActions = ({ player: _player }: { player: Player }) => {
    const context = useFormikContext<Player>();

    return (
      <HStack mt={4}>
        <Tooltip label="Save" placement="top">
          <Button
            width="100%"
            hidden={!context.dirty}
            variant="solid"
            colorScheme="green"
            onClick={() => context.submitForm()}
          >
            <Icon as={IoMdSave} />
          </Button>
        </Tooltip>
        <Tooltip label="Reset" placement="top">
          <Button
            width="100%"
            type="reset"
            hidden={!context.dirty}
            variant="solid"
          >
            <Icon as={ImRedo} />
          </Button>
        </Tooltip>
        <Tooltip label="Delete" placement="top">
          <Button
            width="100%"
            onClick={() => removePlayerAction(_player.id)}
            variant="solid"
            colorScheme="red"
          >
            <Icon as={AiFillDelete} />
          </Button>
        </Tooltip>
      </HStack>
    );
  };

  return (
    <Formik
      initialValues={player}
      onSubmit={(values, actions) => {
        modifyPlayerAction(values);
        actions.resetForm();
      }}
      enableReinitialize
      validationSchema={modifyPlayerValidationSchema}
    >
      <Box
        className=""
        width="100%"
        id={`character-${player.id}`}
        border={`0.3em solid #00AA00${params.id === player.id ? 'FF' : 22}`}
        borderRadius={10}
        p={4}
      >
        <Form>
          <HStack
            p={1}
            mb={2}
          >
            <Box
              textAlign="center"
              width="100%"
              backgroundColor="darkgreen"
              color="white"
              borderRadius={8}
              p={2}
            >
              <Text fontWeight="bold">{player.name}</Text>
            </Box>
            <PlayerModifyActions player={player} />
          </HStack>
          <SimpleGrid templateColumns="repeat(3, 1fr)" mt={4} mb={4}>
            <GridItem>
              <VStack alignItems="flex-start">
                <Box>
                  <HStack>
                    <Text fontWeight="bold">THP </Text>
                    <NumberInputControl hideError name="tempHP" width="6em" />
                  </HStack>
                </Box>
                <Box>
                  <HStack>
                    <Text fontWeight="bold">HP </Text>
                    <NumberInputControl hideError name="currentHP" width="6em" />
                    <Text>
                      /
                      {' '}
                      {player.maxHP}
                    </Text>
                  </HStack>
                </Box>
                <Text>
                  AC
                  {' '}
                  {player.armourClass}
                  , Init.
                  {' '}
                  {player.initiative + player.initiativeMod}
                  {' '}
                  (
                  {player.initiative}
                  <Icon as={GiDiceTwentyFacesTwenty} />
                  {' '}
                  {player.initiativeMod >= 0 ? '+' : '-'}
                  {' '}
                  {Math.abs(player.initiativeMod)}
                  )
                </Text>
              </VStack>
            </GridItem>
            <GridItem colSpan={2}>
              <TextareaControl
                name="notes"
                textareaProps={{
                  resize: 'none',
                  height: '10em',
                }}
              />
            </GridItem>
          </SimpleGrid>
          <ModifyCharacterConditionsField character={player} />
          <Accordion allowToggle allowMultiple>
            <PlayerModifyStatFields />
            <AccordionItem>
              <AccordionButton>
                <Box flex="1" textAlign="left">
                  <strong>Options</strong>
                </Box>
                <AccordionIcon />
              </AccordionButton>
              <AccordionPanel>
                <Grid
                  fontSize="sm"
                  templateColumns="repeat(3, 1fr)"
                  templateRows="repeat(1, 1fr)"
                  gap={6}
                >
                  {
                    ([
                      ['Show in Turn Order', 'displayOnTurnOrder'],
                    ] as Array<[string, keyof Player]>).map(([displayString, key]) => (
                      <FormControl display="flex" alignItems="center" key={key}>
                        <FormLabel mb="0">
                          {displayString}
                        </FormLabel>
                        <Spacer />
                        <SwitchControl name={key} />
                      </FormControl>
                    ))
                  }
                </Grid>
              </AccordionPanel>
            </AccordionItem>
          </Accordion>
        </Form>
      </Box>
    </Formik>
  );
};
