import {
  Badge, Box, Grid, HStack, Icon, Text,
} from '@chakra-ui/react';
import React from 'react';
import { BsHeartFill } from 'react-icons/bs';
import { GiCheckedShield, GiDiceTwentyFacesTwenty, GiWalkingBoot } from 'react-icons/gi';
import { Player } from '../Characters/Player.interfaces';

interface PlayerTurnProps {
  player: Player;
  isActive: boolean;
  onClick: () => void;
}

export function PlayerTurn({ player, isActive, onClick }: PlayerTurnProps) {
  const tempHP = player.tempHP ? ` (${player.tempHP})` : '';

  return (
    <Box
      onClick={onClick}
      className="turn-block turn-block-player"
      border={`${isActive ? '0.4em' : '0.2em'} solid #00AA00${isActive ? 'FF' : 22}`}
      borderRadius="0.4em"
      opacity={isActive ? '100%' : '80%'}
      cursor="pointer"
    >
      <Grid templateColumns="repeat(4, 1fr)" fontSize="sm">
        <Text fontWeight="bold">{player.name}</Text>
        <Text>
          <Icon as={GiCheckedShield} />
          {' '}
          {player.armourClass}
        </Text>
        <Text>
          <Icon as={BsHeartFill} />
          {' '}
          {player.currentHP}
          {' '}
          {tempHP}
          {' '}
          /
          {' '}
          {player.maxHP}
        </Text>
        <Text>
          <Icon as={GiWalkingBoot} />
          {' '}
          {player.initiative + player.initiativeMod}
          {' '}
          (
          {player.initiative}
          <Icon as={GiDiceTwentyFacesTwenty} />
          {' '}
          {player.initiativeMod >= 0 ? '+' : '-'}
          {' '}
          {Math.abs(player.initiativeMod)}
          )
        </Text>
      </Grid>
      {
        player.conditions.length > 0 && (
        <HStack>
          {player.conditions.map((i) => <Badge variant="solid" key={player.conditions.findIndex((j) => i === j)}>{i}</Badge>)}
        </HStack>
        )
      }
    </Box>
  );
}
