import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { v4 } from 'uuid';
import * as Yup from 'yup';
import { Player } from '../Characters/Player.interfaces';
import { Dice, rollDice } from './util';

export interface PlayerState {
  players: Player[];
}

const initialState: PlayerState = {
  players: [],
};

export const playerSlice = createSlice({
  name: 'player',
  initialState,
  reducers: {
    addPlayer: (state, action: PayloadAction<Omit<Player, 'id'>>) => {
      const id = v4();
      state.players.push({
        ...action.payload,
        id,
      });
    },
    removePlayer: (state, action: PayloadAction<string>) => {
      const index = state.players.findIndex((v) => v.id === action.payload);

      if (index >= 0) {
        state.players.splice(index, 1);
      } else {
        console.warn(`Invalid player ${action.payload}`);
      }
    },
    refreshPlayers: (state, action: PayloadAction<Player[] | undefined>) => {
      if (action.payload) {
        state.players.splice(0, state.players.length, ...action.payload);
      }
    },
    modifyPlayer: (state, action: PayloadAction<Partial<Player>>) => {
      const foundPlayer = state.players.findIndex((i) => i.id === action.payload.id);

      if (foundPlayer >= 0) {
        const { players } = state;
        players[foundPlayer] = {
          ...state.players[foundPlayer],
          ...action.payload,
        };
      }
    },
    rollAllPlayerInitiative: (state) => {
      state.players.forEach((i) => {
        const player = i;
        player.initiative = rollDice(Dice.d20);
      });
    },
  },
});

export const modifyPlayerValidationSchema = Yup.object({
  name: Yup.string(),
  currentHP: Yup.number().integer().required(),
  maxHP: Yup.number().integer().required(),
  tempHP: Yup.number().integer().default(0).required(),
  armourClass: Yup.number().integer().required(),
  initiative: Yup.number().integer().min(1).max(20)
    .required(),
  initiativeMod: Yup.number().integer().required(),
  notes: Yup.string(),
  conditions: Yup.array().of(Yup.string()).required(),
  displayOnTurnOrder: Yup.boolean().required(),
});

export const {
  addPlayer, removePlayer, refreshPlayers, rollAllPlayerInitiative, modifyPlayer,
} = playerSlice.actions;

export default playerSlice.reducer;
