import {
  Grid, Flex, Button, useDisclosure, AlertDialog,
  AlertDialogBody, AlertDialogContent, AlertDialogFooter,
  AlertDialogHeader, AlertDialogOverlay,
} from '@chakra-ui/react';
import React, { useEffect, useState, useRef } from 'react';
import { useAppSelector } from '../common/hooks';
import { APP_VERSION, RootState, STORE_KEY } from '../common/store';

interface StateFormat {
  version: string;
  date: number;
  state: RootState;
}

const AlertDeleteState = ({ confirmAction }: { confirmAction: () => void }) => {
  const { isOpen, onOpen, onClose } = useDisclosure();
  const cancelRef = useRef(null);
  return (
    <>
      <Button
        width="100%"
        colorScheme="red"
        onClick={onOpen}
      >
        Clear State
      </Button>
      <AlertDialog
        isOpen={isOpen}
        leastDestructiveRef={cancelRef}
        onClose={onClose}
      >
        <AlertDialogOverlay>
          <AlertDialogContent>
            <AlertDialogHeader fontSize="lg" fontWeight="bold">
              Clear State
            </AlertDialogHeader>
            <AlertDialogBody>
              Are you sure? This action cannot be undone.
            </AlertDialogBody>
            <AlertDialogFooter>
              <Button ref={cancelRef} onClick={onClose}>
                Cancel
              </Button>
              <Button
                colorScheme="red"
                onClick={() => {
                  onClose();
                  confirmAction();
                }}
                ml={3}
              >
                Delete
              </Button>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialogOverlay>
      </AlertDialog>
    </>
  );
};

export const Settings = () => {
  const state = useAppSelector((s) => s);

  const exportStateFormat = (): StateFormat => ({
    version: APP_VERSION,
    state,
    date: Date.now(),
  });

  const [exportStateLink, setExportStateLink] = useState('');

  const exportStateRef = useRef<HTMLAnchorElement>(null);
  const importStateRef = useRef<HTMLInputElement>(null);
  const makeExportStateFileName = () => `${Date.now()}_${APP_VERSION}_${state.campaign.campaignTitle}.json`;
  const makeExportStateFile = () => {
    // This creates the file.
    // In my case, I have an array, and each item in
    // the array should be on a new line, which is why
    // I use .join('\n') here.
    const data = new Blob([JSON.stringify(exportStateFormat())], { type: 'application/json' });

    // this part avoids Memory Leaks
    if (exportStateLink !== '') window.URL.revokeObjectURL(exportStateLink);

    // update the download link state
    setExportStateLink(window.URL.createObjectURL(data));
  };

  const clearState = () => {
    localStorage.removeItem(`${STORE_KEY}_${APP_VERSION}`);
    window.location.reload();
  };

  const importState = (importedState: string) => {
    try {
      const parsedImportedState: StateFormat = JSON.parse(importedState);
      localStorage.setItem(`${STORE_KEY}_${APP_VERSION}`, JSON.stringify(parsedImportedState.state));
      window.location.reload();
    } catch (e) {
      console.error('Failed to import state');
    }
  };

  useEffect(() => {
    makeExportStateFile();
  }, [state]);

  return (
    <Grid
      templateColumns="repeat(1, 1fr)"
      gap={5}
    >
      <Flex>
        <input
          type="file"
          accept="application/json"
          onChange={async (e) => {
            e.preventDefault();
            const reader = new FileReader();
            reader.onload = async (_e) => {
              const text = _e?.target?.result;
              if (typeof text === 'string') {
                importState(text as string);
              }
            };
            reader.readAsText(importStateRef.current?.files?.[0] as Blob);
          }}
          ref={importStateRef}
          hidden
        />
        <Button
          width="100%"
          onClick={() => {
            importStateRef.current?.click();
          }}
        >
          Import Saved State
        </Button>
      </Flex>
      {/* <Flex>
        <Button width="100%">Import Characters</Button>
      </Flex> */}
      <Flex>
        <a
          hidden
          download={makeExportStateFileName()}
          href={exportStateLink}
          ref={exportStateRef}
        >
          Export
        </a>
        <Button width="100%" onClick={() => exportStateRef.current?.click()}>Export Save State</Button>
      </Flex>
      <AlertDeleteState confirmAction={clearState} />
    </Grid>
  );
};
