import {
  Center, Container, Divider, Heading,
} from '@chakra-ui/react';
import { FaBars, FaCog } from 'react-icons/fa';
import React, { ReactElement } from 'react';
import {
  BrowserRouter as Router, Route, Switch,
} from 'react-router-dom';
import { IconType } from 'react-icons';
import { BsPersonFill } from 'react-icons/bs';
import { GiBirdClaw } from 'react-icons/gi';
import { AiFillRobot } from 'react-icons/ai';
import { NPCs } from '../Characters/NPCs';
import { Players } from '../Characters/Players';
import { setCampaignTitle, setTurn } from '../common/campaignSlice';
import { useAppDispatch } from '../common/hooks';
import { refreshNPCs } from '../common/npcSlice';
import { refreshPlayers } from '../common/playerSlice';
import { loadFromLocalStorage, RootState } from '../common/store';
import { Initiative } from '../Initiative';
import { Settings } from '../Settings';
import { CampaignTitle } from './CampaignTitle';
import { Sidebar } from './Sidebar';
import './App.css';

export function App() {
  const dispatch = useAppDispatch();

  window.addEventListener('storage', () => {
    const persistedStorage: RootState = loadFromLocalStorage();

    if (!document.hasFocus()) {
      const refreshActions = [
        refreshPlayers(persistedStorage?.players?.players || []),
        refreshNPCs(persistedStorage?.npcs?.npcs || []),
        setCampaignTitle(persistedStorage?.campaign?.campaignTitle || ''),
        setTurn(persistedStorage?.campaign?.currentTurnId || ''),
      ];
      refreshActions.map(dispatch);
    }
  });

  const routes: Array<{
    component: ReactElement,
    basePath: string,
    path: string,
    exact?: boolean,
    pageTitle: string,
    icon?: IconType,
  }> = [
    {
      component: <Initiative />,
      path: '/',
      basePath: '/',
      exact: true,
      pageTitle: 'Turn Order',
      icon: FaBars,
    },
    // {
    //   component: <DMScreen />,
    //   path: '/screen',
    //   basePath: '/screen',
    //   pageTitle: 'DM Screen',
    //   icon: FaRegMap,
    // },
    {
      component: <Players />,
      path: '/players',
      basePath: '/players/:id?',
      pageTitle: 'Players',
      icon: BsPersonFill,
    },
    {
      component: <NPCs showHostile />,
      path: '/enemies',
      basePath: '/enemies/:id?',
      pageTitle: 'Enemies',
      icon: GiBirdClaw,
    },
    {
      component: <NPCs showFriendly showNeutral />,
      path: '/npcs',
      basePath: '/npcs/:id?',
      pageTitle: 'Other NPCs',
      icon: AiFillRobot,
    },
    {
      component: <Settings />,
      path: '/settings',
      basePath: '/settings',
      pageTitle: 'Settings',
      icon: FaCog,
    },
  ];

  return (
    <div className="App">
      <Router
        basename="/dndtracker"
      >
        <Sidebar routes={routes} />
        <Container maxW="800" minW="650">
          <CampaignTitle />
          <Switch>
            {
              routes.map(({
                component, basePath, pageTitle, exact,
              }) => (
                <Route
                  path={basePath}
                  exact={exact}
                  key={pageTitle}
                >
                  <Center><Heading size="md">{pageTitle}</Heading></Center>
                  <Divider mt={4} mb={4} />
                  {component}
                </Route>
              ))
            }
          </Switch>
        </Container>
      </Router>
    </div>
  );
}
