import { createSlice, PayloadAction } from '@reduxjs/toolkit';

interface CampaignState {
  currentTurnId: string;
  campaignTitle: string,
}

const initialState: CampaignState = {
  currentTurnId: '',
  campaignTitle: 'D&D Tracker',
};

export const campaignSlice = createSlice({
  name: 'campaign',
  initialState,
  reducers: {
    setTurn: (state, action: PayloadAction<string>) => {
      const modState = state;
      modState.currentTurnId = action.payload;
    },
    setCampaignTitle: (state, action: PayloadAction<string>) => {
      const modState = state;
      modState.campaignTitle = action.payload;
    },
  },
});

export const { setTurn, setCampaignTitle } = campaignSlice.actions;

export default campaignSlice.reducer;
