import {
  Badge, Box, Grid, HStack, Icon, Text,
} from '@chakra-ui/react';
import React from 'react';
import { BsHeartFill } from 'react-icons/bs';
import { GiCheckedShield, GiDiceTwentyFacesTwenty, GiWalkingBoot } from 'react-icons/gi';
import { npcTypeColours } from '../App';
import { NPC } from '../Characters/NPC.interfaces';

interface NPCTurnProps {
  npc: NPC;
  isActive: boolean;
  onClick: () => void;
}

export function NPCTurn({ npc, isActive, onClick }: NPCTurnProps) {
  const tempHP = npc.tempHP ? ` (${npc.tempHP})` : '';

  let formColour = npcTypeColours.neutral;

  if (npc.isHostile) {
    formColour = npcTypeColours.enemy;
  } else if (npc.isFriendly) {
    formColour = npcTypeColours.friendly;
  }

  return (
    <Box
      className="turn-block turn-block-npc"
      border={`${isActive ? '0.4em' : '0.2em'} solid ${formColour}${isActive ? 'FF' : 22}`}
      borderRadius="0.4em"
      opacity={isActive ? '100%' : '80%'}
      cursor="pointer"
      onClick={onClick}
    >
      <Grid templateColumns="repeat(3, 1fr)" fontSize="sm">
        <Text fontWeight="bold">{npc.showName ? npc.name : '???'}</Text>
        {
          npc.showAC
          && (
          <Text>
            <Icon as={GiCheckedShield} />
            {' '}
            {npc.armourClass}
          </Text>
          )
        }
        {
          npc.showHealth
            ? (
              <Text>
                <Icon as={BsHeartFill} />
                {' '}
                {npc.currentHP}
                {' '}
                {tempHP}
                {' '}
                /
                {' '}
                {npc.maxHP}
              </Text>
            )
            : (
              <Text>
                <Icon as={BsHeartFill} />
                {' '}
                Damage Taken:
                {' '}
                {npc.maxHP - npc.currentHP}
              </Text>
            )
        }
        {
          npc.showInitiative
          && (
          <Text>
            <Icon as={GiWalkingBoot} />
            {' '}
            {npc.initiative + npc.initiativeMod}
            {' '}
            (
            {npc.initiative}
            <Icon as={GiDiceTwentyFacesTwenty} />
            {' '}
            {npc.initiativeMod >= 0 ? '+' : '-'}
            {' '}
            {Math.abs(npc.initiativeMod)}
            )
          </Text>
          )
        }
      </Grid>
      {
        npc.conditions.length > 0 && (
        <HStack>
          {npc.conditions.map((i) => <Badge variant="solid" key={npc.conditions.findIndex((j) => i === j)}>{i}</Badge>)}
        </HStack>
        )
      }
    </Box>
  );
}
