import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { v4 } from 'uuid';
import * as Yup from 'yup';
import { Dice, rollDice } from './util';
import { NPC } from '../Characters/NPC.interfaces';

export interface NPCState {
  npcs: NPC[];
}

const initialState: NPCState = {
  npcs: [],
};

export const npcSlice = createSlice({
  name: 'npc',
  initialState,
  reducers: {
    addNPC: (state, action: PayloadAction<Omit<NPC, 'id'>>) => {
      const id = v4();
      state.npcs.push({
        ...action.payload,
        id,
      });
    },
    removeNPC: (state, action: PayloadAction<string>) => {
      const index = state.npcs.findIndex((v) => v.id === action.payload);

      if (index >= 0) {
        state.npcs.splice(index, 1);
      } else {
        console.warn(`Invalid NPC ${action.payload}`);
      }
    },
    refreshNPCs: (state, action: PayloadAction<NPC[] | undefined>) => {
      if (action.payload) {
        state.npcs.splice(0, state.npcs.length, ...action.payload);
      }
    },
    modifyNPC: (state, action: PayloadAction<Partial<NPC>>) => {
      const foundNPC = state.npcs.findIndex((i) => i.id === action.payload.id);

      if (foundNPC >= 0) {
        const { npcs } = state;
        npcs[foundNPC] = {
          ...state.npcs[foundNPC],
          ...action.payload,
        };
      }
    },
    rollAllNPCInitiative: (state) => {
      state.npcs.forEach((i) => {
        const npc = i;
        npc.initiative = rollDice(Dice.d20);
      });
    },
  },
});

export const modifyNPCValidationSchema = Yup.object({
  name: Yup.string(),
  currentHP: Yup.number().integer().required(),
  maxHP: Yup.number().integer().required(),
  tempHP: Yup.number().integer().default(0).required(),
  armourClass: Yup.number().integer().required(),
  initiative: Yup.number().integer().min(1).max(20)
    .required(),
  initiativeMod: Yup.number().integer().required(),
  notes: Yup.string(),
  conditions: Yup.array().of(Yup.string()).required(),
  displayOnTurnOrder: Yup.boolean().required(),
  isHostile: Yup.boolean().required(),
  isFriendly: Yup.boolean().required(),
  isBoss: Yup.boolean().required(),
  showHealth: Yup.boolean().required(),
  showInitiative: Yup.boolean().required(),
  showAC: Yup.boolean().required(),
  showName: Yup.boolean().required(),
});

export const {
  addNPC, removeNPC, refreshNPCs, rollAllNPCInitiative, modifyNPC,
} = npcSlice.actions;

export default npcSlice.reducer;
