import { AddIcon } from '@chakra-ui/icons';
import {
  Button, HStack, Icon, VStack,
} from '@chakra-ui/react';
import { Form, Formik } from 'formik';
import React from 'react';
import * as Yup from 'yup';
import { InputControl, NumberInputControl } from '../common/formik-chakra';
import { useAppDispatch, useAppSelector } from '../common/hooks';
import { addPlayer } from '../common/playerSlice';
import { Dice, rollDice } from '../common/util';
import { ModifyPlayerForm } from './ModifyPlayerForm';
import { Player } from './Player.interfaces';

const PlayerFormList = ({ players }: { players: Player[] }) => (
  <VStack>
    {
        players.map((player) => (
          <ModifyPlayerForm
            key={player.id}
            player={player}
          />
        ))
      }
  </VStack>
);

export function Players() {
  const players = useAppSelector((state) => state.players.players);
  const dispatch = useAppDispatch();

  const addPlayerValidationSchema = Yup.object({
    name: Yup.string().required().label('Name'),
    maxHP: Yup.number().integer().moreThan(0).required()
      .label('Maximum HP'),
    initiativeMod: Yup.number().integer().required().label('Initiative Modifier'),
    armourClass: Yup.number().integer().moreThan(0).default(10)
      .label('Armour Class'),
  });

  type AddFormFields = Array<[
    string, keyof Player, typeof InputControl | typeof NumberInputControl]
  >;

  const AddPlayerForm = () => {
    const addFormFields: AddFormFields = [
      ['Player Name', 'name', InputControl],
      ['Maximum HP', 'maxHP', NumberInputControl],
      ['Initiative Modifier', 'initiativeMod', NumberInputControl],
      ['Armour Class', 'armourClass', NumberInputControl],
    ];
    return (
      <Formik
        initialValues={{
          name: '',
          maxHP: 0,
          initiativeMod: 0,
          armourClass: 0,
        }}
        validationSchema={addPlayerValidationSchema}
        onSubmit={
          (values) => {
            dispatch(addPlayer({
              name: values.name,
              maxHP: values.maxHP,
              currentHP: values.maxHP,
              tempHP: 0,
              initiative: rollDice(Dice.d20),
              initiativeMod: values.initiativeMod,
              armourClass: values.armourClass,
              conditions: [],
              displayOnTurnOrder: true,
              notes: '',
            }));
          }
        }
      >
        <Form>
          <HStack>
            {
              addFormFields.map(([label, key, IC]) => (
                <IC
                  hideError
                  mt={4}
                  mb={4}
                  name={key}
                  label={label}
                  key={key}
                />
              ))
            }
          </HStack>
          <Button
            mb={4}
            width="100%"
            type="submit"
            variant="solid"
            colorScheme="green"
            rightIcon={<Icon as={AddIcon} />}
          >
            Add Player
          </Button>
        </Form>
      </Formik>
    );
  };

  return (
    <>
      <AddPlayerForm />
      <PlayerFormList players={players} />
    </>
  );
}
