import { AddIcon } from '@chakra-ui/icons';
import {
  Text, Box, Flex, Divider, Tag, TagLabel, TagCloseButton, Input, TagRightIcon, InputProps,
} from '@chakra-ui/react';
import { useFormikContext, FieldArray } from 'formik';
import React from 'react';
import { Character } from './Character.interfaces';
import { NPC } from './NPC.interfaces';

export const ModifyCharacterConditionsField = ({ character }: { character: Character }) => {
  const context = useFormikContext<NPC>();
  return (
    <FieldArray
      name="conditions"
      render={(arrayHelpers) => {
        const addCondition = () => {
          const element = document.querySelector(`#tag-input-${character.id}`) as InputProps;
          if (element?.value) {
            const tagInput = element.value.toString();
            element.value = '';
            arrayHelpers.push(tagInput);
          }
        };

        return (
          <Box p={2} border="1px solid #00000066" borderRadius={8} mb={2}>
            <Flex justifyContent="center"><Text fontWeight="bold">Conditions</Text></Flex>
            <Divider mt={1} mb={1} opacity="0.3" />
            {context.values.conditions.map((condition, idx) => (
              <Tag
                variant="solid"
                borderRadius="full"
                size="lg"
                m={2}
                key={condition}
              >
                <TagLabel>{condition}</TagLabel>
                <TagCloseButton onClick={() => arrayHelpers.remove(idx)} />
              </Tag>
            ))}
            <Tag
              variant="outline"
              borderRadius="full"
              size="lg"
              m={2}
            >
              <Input
                size="xs"
                id={`tag-input-${character.id}`}
                onKeyPress={(e) => {
                  if (e.code === 'Enter') {
                    addCondition();
                  }
                }}
              />
              <TagRightIcon
                as={AddIcon}
                cursor="pointer"
                onClick={() => {
                  addCondition();
                }}
              />
            </Tag>
          </Box>
        );
      }}
    />
  );
};
