import {
  Flex, Heading, IconButton, Icon, Input,
} from '@chakra-ui/react';
import React, { useState } from 'react';
import { GoPencil } from 'react-icons/go';
import { ImRedo } from 'react-icons/im';
import { IoMdSave } from 'react-icons/io';
import { setCampaignTitle } from '../common/campaignSlice';
import { useAppSelector, useAppDispatch } from '../common/hooks';

export const CampaignTitle = () => {
  const campaignTitle = useAppSelector((state) => state.campaign.campaignTitle);
  const dispatch = useAppDispatch();
  const setTitleAction = (ct: string) => dispatch(setCampaignTitle(ct));
  const [editableCampaignTitle, setEditableCampaignTitle] = useState(campaignTitle);
  const [editTitleEnabled, setEditTitleEnabled] = useState(false);

  const editTitleRef = React.useRef<HTMLInputElement>(null);
  return (
    <Flex
      mb={4}
      width="100%"
      justifyContent="center"
      alignItems="center"
    >
      {
        !editTitleEnabled && (
          <Flex
            justifyContent="center"
            alignItems="center"
          >
            <Heading>{campaignTitle}</Heading>
            <IconButton
              ml={2}
              aria-label="Edit Title"
              icon={<Icon as={GoPencil} />}
              onClick={() => {
                setEditTitleEnabled(true);
                setTimeout(() => {
                  editTitleRef.current?.focus();
                }, 100);
              }}
            />
          </Flex>
        )
      }
      {
        editTitleEnabled && (
          <Flex
            justifyContent="center"
            alignItems="center"
          >
            <Input
              ref={editTitleRef}
              onChange={(e) => setEditableCampaignTitle(e.target.value)}
              onKeyPress={(e) => {
                if (e.code === 'Enter') {
                  setEditTitleEnabled(false);
                  setTitleAction(editableCampaignTitle);
                }
              }}
            />
            <IconButton
              ml={2}
              aria-label="Save"
              icon={<Icon as={IoMdSave} />}
              onClick={() => {
                setEditTitleEnabled(false);
                setTitleAction(editableCampaignTitle);
              }}
            />
            <IconButton
              ml={2}
              aria-label="Reset"
              icon={<Icon as={ImRedo} />}
              onClick={() => setEditTitleEnabled(false)}
            />
          </Flex>
        )
      }
    </Flex>
  );
};
