import { configureStore } from '@reduxjs/toolkit';
import playerReducer from './playerSlice';
import campaignReducer from './campaignSlice';
import npcReducer from './npcSlice';

export const STORE_KEY = 'persistantState';
export const APP_VERSION = '0.0.2';

export const loadFromLocalStorage = () => {
  try {
    const serialisedState = localStorage.getItem(`persistantState_${APP_VERSION}`);
    if (serialisedState === null) return undefined;
    return JSON.parse(serialisedState);
  } catch (e) {
    console.warn(e);
    return undefined;
  }
};

export const store = configureStore({
  reducer: {
    players: playerReducer,
    campaign: campaignReducer,
    npcs: npcReducer,
  },
  preloadedState: loadFromLocalStorage(),
});

export type RootState = ReturnType<typeof store.getState>;
export type AppDispatch = typeof store.dispatch;

function saveToLocalStorage(state: RootState) {
  try {
    const serialisedState = JSON.stringify(state);
    localStorage.setItem(`persistantState_${APP_VERSION}`, serialisedState);
  } catch (e) {
    console.warn(e);
  }
}

store.subscribe(() => {
  if (document.hasFocus()) {
    saveToLocalStorage(store.getState());
  }
});
