export enum Dice {
  d100 = 100,
  d20 = 20,
  d12 = 12,
  d10 = 10,
  d8 = 8,
  d6 = 6,
  d4 = 4,
  d2 = 2
}

export const rollDice = (max: number): number => Math.ceil(Math.random() * max);

export const rollAdvantage = (max: number): number => Math.max(rollDice(max), rollDice(max));

export const rollDisadvantage = (max: number): number => Math.min(rollDice(max), rollDice(max));
