import { AddIcon } from '@chakra-ui/icons';
import {
  Button, HStack, Icon, VStack,
} from '@chakra-ui/react';
import { Form, Formik } from 'formik';
import React from 'react';
import * as Yup from 'yup';
import { InputControl, NumberInputControl } from '../common/formik-chakra';
import { useAppDispatch, useAppSelector } from '../common/hooks';
import { addNPC } from '../common/npcSlice';
import { Dice, rollDice } from '../common/util';
import { ModifyNPCForm } from './ModifyNPCForm';
import { NPC } from './NPC.interfaces';

const NPCFormList = ({ npcs }: { npcs: NPC[] }) => (
  <VStack>
    {
        npcs.map((npc) => (
          <ModifyNPCForm
            key={npc.id}
            npc={npc}
          />
        ))
      }
  </VStack>
);

interface NPCsProps {
  showHostile?: boolean;
  showFriendly?: boolean;
  showNeutral?: boolean;
}

export function NPCs({
  showHostile, showFriendly, showNeutral,
}: NPCsProps) {
  const npcs = useAppSelector((state) => state.npcs.npcs.filter((i) => {
    if (showHostile && i.isHostile) {
      return true;
    }
    if (showFriendly && i.isFriendly) {
      return true;
    }
    if (showNeutral && (!i.isFriendly && !i.isHostile)) {
      return true;
    }
    return false;
  }));
  const dispatch = useAppDispatch();

  const addNPCValidationSchema = Yup.object({
    name: Yup.string().required().label('Name'),
    maxHP: Yup.number().integer().moreThan(0).required()
      .label('Maximum HP'),
    initiativeMod: Yup.number().integer().required().label('Initiative Modifier'),
    armourClass: Yup.number().integer().moreThan(0).default(10)
      .label('Armour Class'),
  });

  const AddNPCForm = () => {
    type AddFormFields = Array<
      [string, keyof NPC, typeof InputControl | typeof NumberInputControl]
    >;

    const addFormFields: AddFormFields = [
      ['NPC Name', 'name', InputControl],
      ['Maximum HP', 'maxHP', NumberInputControl],
      ['Initiative Modifier', 'initiativeMod', NumberInputControl],
      ['Armour Class', 'armourClass', NumberInputControl],
    ];
    return (
      <Formik
        initialValues={{
          name: '',
          maxHP: 0,
          initiativeMod: 0,
          armourClass: 0,
        }}
        validationSchema={addNPCValidationSchema}
        onSubmit={
          (values) => {
            dispatch(addNPC({
              name: values.name,
              maxHP: values.maxHP,
              currentHP: values.maxHP,
              tempHP: 0,
              initiative: rollDice(Dice.d20),
              initiativeMod: values.initiativeMod,
              armourClass: values.armourClass,
              conditions: [],
              displayOnTurnOrder: true,
              notes: '',
              isFriendly: showFriendly || false,
              isHostile: showHostile || false,
              isBoss: false,
              showHealth: false,
              showInitiative: false,
              showAC: false,
              showName: false,
            }));
          }
        }
      >
        <Form>
          <HStack>
            {
              addFormFields.map(([label, key, IC]) => (
                <IC
                  hideError
                  mt={4}
                  mb={4}
                  name={key}
                  label={label}
                  key={key}
                />
              ))
            }
          </HStack>
          <Button
            mb={4}
            width="100%"
            type="submit"
            variant="solid"
            colorScheme="green"
            rightIcon={<Icon as={AddIcon} />}
          >
            Add
            {' '}
            {showHostile ? 'Enemy' : 'NPC'}
          </Button>
        </Form>
      </Formik>
    );
  };

  return (
    <>
      <AddNPCForm />
      <NPCFormList npcs={npcs} />
    </>
  );
}
