import {
  Accordion, AccordionButton, AccordionIcon, AccordionItem,
  AccordionPanel, Box, Button, FormControl, FormLabel, Grid,
  GridItem, HStack, Icon, SimpleGrid, Spacer, Text, Tooltip, VStack,
} from '@chakra-ui/react';
import { Form, Formik, useFormikContext } from 'formik';
import { AiFillDelete } from 'react-icons/ai';
import { GiDiceTwentyFacesTwenty } from 'react-icons/gi';
import { ImRedo } from 'react-icons/im';
import { IoMdSave } from 'react-icons/io';
import React from 'react';
import { useParams } from 'react-router-dom';
import { NumberInputControl, SwitchControl } from '../common/formik-chakra';
import TextareaControl from '../common/formik-chakra/TextareaControl';
import { useAppDispatch } from '../common/hooks';
import { modifyNPC, removeNPC, modifyNPCValidationSchema } from '../common/npcSlice';
import { Dice, rollDice } from '../common/util';
import { ModifyCharacterConditionsField } from './ModifyCharacterConditionsField';
import { NPC } from './NPC.interfaces';
import { npcTypeColours } from '../App';

interface ModifyNPCFormProps {
  npc: NPC;
}

export const ModifyNPCForm = ({ npc }: ModifyNPCFormProps) => {
  const dispatch = useAppDispatch();

  const params = useParams<{id: string}>();

  const modifyNPCAction = (n: NPC) => dispatch(modifyNPC(n));
  const removeNPCAction = (id: string) => dispatch(removeNPC(id));

  let formColour = npcTypeColours.neutral;

  if (npc.isHostile) {
    formColour = npcTypeColours.enemy;
  } else if (npc.isFriendly) {
    formColour = npcTypeColours.friendly;
  }

  const NPCModifyStatFields = () => {
    const editStatFields: Array<[string, string]> = [
      ['AC: ', 'armourClass'],
      ['Initiative: ', 'initiative'],
      ['Init. Mod: ', 'initiativeMod'],
      ['Max HP: ', 'maxHP'],
    ];

    const { setFieldValue } = useFormikContext<NPC>();
    return (
      <AccordionItem>
        <AccordionButton>
          <Box flex="1" textAlign="left">
            <strong>Edit Stats</strong>
          </Box>
          <AccordionIcon />
        </AccordionButton>
        <AccordionPanel>
          <Grid
            fontSize="sm"
            templateColumns="repeat(4, 1fr)"
            templateRows="repeat(2, 1fr)"
            gap={4}
          >
            <GridItem
              colSpan={4}
            >
              <Button
                width="100%"
                onClick={() => setFieldValue('initiative', rollDice(Dice.d20))}
                variant="outline"
                rightIcon={<Icon as={GiDiceTwentyFacesTwenty} />}
              >
                Roll Initiative
              </Button>
            </GridItem>
            {
              editStatFields.map(([displayString, value]) => (
                <GridItem key={value}>
                  <HStack>
                    <Text>{displayString}</Text>
                    <NumberInputControl hideError name={value} width="8em" />
                  </HStack>
                </GridItem>
              ))
            }
          </Grid>
        </AccordionPanel>
      </AccordionItem>
    );
  };

  const NPCModifyActions = ({ npc: _npc }: { npc: NPC }) => {
    const context = useFormikContext<NPC>();

    return (
      <HStack mt={4}>
        <Tooltip label="Save" placement="top">
          <Button
            width="100%"
            hidden={!context.dirty}
            variant="solid"
            colorScheme="green"
            onClick={() => context.submitForm()}
          >
            <Icon as={IoMdSave} />
          </Button>
        </Tooltip>
        <Tooltip label="Reset" placement="top">
          <Button
            width="100%"
            type="reset"
            hidden={!context.dirty}
            variant="solid"
          >
            <Icon as={ImRedo} />
          </Button>
        </Tooltip>
        <Tooltip label="Delete" placement="top">
          <Button
            width="100%"
            onClick={() => removeNPCAction(_npc.id)}
            variant="solid"
            colorScheme="red"
          >
            <Icon as={AiFillDelete} />
          </Button>
        </Tooltip>
      </HStack>
    );
  };

  return (
    <Formik
      initialValues={npc}
      onSubmit={(values, actions) => {
        modifyNPCAction(values);
        actions.resetForm();
      }}
      enableReinitialize
      validationSchema={modifyNPCValidationSchema}
    >
      <Box
        className=""
        width="100%"
        id={`character-${npc.id}`}
        border={`0.3em solid ${formColour}${params.id === npc.id ? 'FF' : 22}`}
        borderRadius={10}
        p={4}
      >
        <Form>
          <HStack
            p={1}
            mb={2}
          >
            <Box
              textAlign="center"
              width="100%"
              backgroundColor={formColour}
              color="white"
              borderRadius={8}
              p={2}
            >
              <Text fontWeight="bold">{npc.name}</Text>
            </Box>
            <NPCModifyActions npc={npc} />
          </HStack>
          <SimpleGrid templateColumns="repeat(3, 1fr)" mt={4} mb={4}>
            <GridItem>
              <VStack alignItems="flex-start">
                <Box>
                  <HStack>
                    <Text fontWeight="bold">THP </Text>
                    <NumberInputControl hideError name="tempHP" width="6em" />
                  </HStack>
                </Box>
                <Box>
                  <HStack>
                    <Text fontWeight="bold">HP </Text>
                    <NumberInputControl hideError name="currentHP" width="6em" />
                    <Text>
                      /
                      {' '}
                      {npc.maxHP}
                    </Text>
                  </HStack>
                </Box>
                <Text>
                  AC
                  {' '}
                  {npc.armourClass}
                  , Init.
                  {' '}
                  {npc.initiative + npc.initiativeMod}
                  {' '}
                  (
                  {npc.initiative}
                  <Icon as={GiDiceTwentyFacesTwenty} />
                  {' '}
                  {npc.initiativeMod >= 0 ? '+' : '-'}
                  {' '}
                  {Math.abs(npc.initiativeMod)}
                  )
                </Text>
              </VStack>
            </GridItem>
            <GridItem colSpan={2}>
              <TextareaControl
                name="notes"
                textareaProps={{
                  resize: 'none',
                  height: '10em',
                }}
              />
            </GridItem>
          </SimpleGrid>
          <ModifyCharacterConditionsField character={npc} />
          <Accordion allowToggle allowMultiple>
            <NPCModifyStatFields />
            <AccordionItem>
              <AccordionButton>
                <Box flex="1" textAlign="left">
                  <strong>Options</strong>
                </Box>
                <AccordionIcon />
              </AccordionButton>
              <AccordionPanel>
                <Grid
                  fontSize="sm"
                  templateColumns="repeat(4, 1fr)"
                  templateRows="repeat(2, 1fr)"
                  gap={6}
                >
                  {
                    ([
                      ['Reveal Name', 'showName'],
                      ['Reveal Health', 'showHealth'],
                      ['Reveal Armour Class', 'showAC'],
                      ['Reveal Initiative', 'showInitiative'],
                      ['Show in Turn Order', 'displayOnTurnOrder'],
                      ['Hostile to Party', 'isHostile'],
                      ['Friendly to Party', 'isFriendly'],
                    ] as Array<[string, keyof NPC]>).map(([displayString, key]) => (
                      <FormControl
                        display="flex"
                        alignItems="center"
                        key={key}
                      >
                        <FormLabel mb="0" fontSize="xs">
                          {displayString}
                        </FormLabel>
                        <Spacer />
                        <SwitchControl name={key} />
                      </FormControl>
                    ))
                  }
                </Grid>
              </AccordionPanel>
            </AccordionItem>
          </Accordion>
        </Form>
      </Box>
    </Formik>
  );
};
